@import url(https://fonts.googleapis.com/css?family=Montserrat);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* { font-family: 'Montserrat', sans-serif; }

body, html {
  height: 100%;
  min-height: 100%;
  color: #fff;
}
#root {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
video {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  min-width: 100%;
  min-height: 100%;
}

.navigation {
  padding: 25px 15px;
  z-index: 100;
    color: #fff;
    position: relative;
}
.logo {
  font-size: 1.5em;
  line-height: 1.5em;
}
.menu svg {
  line-height: 1.5em;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: auto;
  margin-bottom: 10%;
  color: #fff;
}
.subtitle {
  margin-bottom: 1%;
  text-transform: uppercase;
  letter-spacing: 4px;
}
h2 {
  text-transform: uppercase;
  font-size: 50px;
  margin-bottom: 15px;
}
.text-muted {
  color: rgba(1, 1, 1, 1) !important;
}
.footer p {
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
}

.btn {
  display: inline-block;
  font-weight: 400;
  color: #fff;
  text-align: center;
  vertical-align: middle;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: .5rem 2.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0;
  transition: color 2s ease-in-out,background-color 2s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}
.btn-white {
  color: #fff;
  background-color: transparent;
  border-color: #fff;
}
.btn:hover {
    color: #212529;
    background-color: #fff;
    text-decoration: none;
}

